function checkPredictions(){
  console.log(predictions.length);
  for(var i=0;i<predictions.length;i++){
    if(predictions[i]==-1)
    return true;
  }
  return false;
}

function makeBet() {
  console.log(predictions);
  if(checkPredictions()){
    $("#betProgress").modal();
    updateBetProgress("assets/img/error.png", "Please select all predictions");
    return;
  }

  $("#betProgress").modal();
  //$('#lottoprogressbar').css('width', 10+'%').attr('aria-valuenow', 10);
  //$('#lottoprogresstext').html("<i className='fa fa-clock-o'></i>  Please wait, while the bets are being placed..")
  updateBetProgress("assets/img/waiting.png", "Please wait, while the bets are being placed");
  console.log(predictions[0]);
    eos.contract(contractName).then((contract) => {

        //$('#lottoprogressbar').css('width', 40+'%').attr('aria-valuenow', 40);
        //$('#lottoprogresstext').html("<i className='fa fa-shield'></i> Getting the required permissions..")
        updateBetProgress("assets/img/permissions.png", "Getting the required permissions");


         eos.updateauth({
                "account": username,
                "permission":"active",
                "parent":"owner",
                "auth":{
                    "keys":[{
                        "key": pubkey,
                        "weight":1
                    }],
                    "threshold":1,
                    "accounts":[{
                        "permission": {
                            "actor": contractName,
                            "permission":"eosio.code"
                        },
                        "weight":1
                    }],
                    "waits":[]
                }
            }, {
                authorization: username+"@active"
            }).then(result => {
            updateBetProgress("assets/img/payment.png", "Sending 0.5 EOS to the contract");

              //$('#lottoprogressbar').css('width', 80+'%').attr('aria-valuenow', 80);
              //$('#lottoprogresstext').html("<i className='fa fa-hourglass-start'> </i> Sending 0.5 EOS to the contract...");
              console.log("predictions");
              console.log(predictions);
              contract.placebet({user:username,bet:"0.5000 EOS",predictions:predictions, referral: referral}, { authorization: [{
                  actor: username,
                  permission: 'active'
                }] }).then(result => {console.log(result);
              updateBetProgress("assets/img/placed.png", "Your bet has been placed");

                //  $('#lottoprogressbar').css('width', 100+'%').attr('aria-valuenow', 100);
                  //$('#lottoprogresstext').html("<i className='fa fa-thumbs-o-up'></i> Bets placed successfully");
                   })
              })

      }).catch((err) => { console.log('ouch') });

    }



