class MatchTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            matches: []
        }
        this.loggedIn = this.loggedIn.bind(this);
    }

    fetchMatches() {
      //  matches = [];
        var self = this;


        this.setState({matches:matches});
        /*
        eos.getTableRows({json:true, code: contract, json:true, scope: contract, table: "games", limit:1000}).then(r => {
            for (var i = 0; i < r.rows.length; i++) {
            matches.push({
              homeTeam:r.rows[i].home_team,
              awayTeam: r.rows[i].away_team,
              time: r.rows[i].match_time,
              prediction: null
            });
            self.setState({
              matches: matches
            });
            }
          });*/
    }

    updatePrediction(key, prediction) {
    /*  this.setState({
        matches: update(this.state.matches, {key: {prediction: {$set: prediction}}})
     });*/
    }

    componentWillMount() {
        if (!this.props.isAdmin)
        this.setState({matches:[]});
         //this.fetchMatches();
    }

    removeMatch(i) {
      matches = this.state.matches;
      matches.splice(i,1);
      this.setState({matches:matches});
    }

    loggedIn(event){
      if (!this.props.isAdmin)
      this.fetchMatches();
    }

    matchAdded(event){
      matches = this.state.matches;
      var match = event.detail;
      matches.push(match);
      this.setState({matches:matches});
    }

    async componentDidMount(){
      this.fetchMatches();
    //  window.addEventListener('loggedIn', this.loggedIn);
    }

    componentWillUnMount(){
      //window.removeEventListener('loggedIn', this.loggedIn);
    }

    render() {

      return (

          <table className="table">
            <thead >
              { this.props.isAdmin &&
                 <AdminTableHeader />
              }
              { !this.props.isAdmin &&
                 <UserTableHeader />
              }
            </thead>
            <tbody>
              {  this.state.matches.map((match, i) =>
                   <MatchSlot
                       key={i}
                       prediction={match.prediction}
                       time={match.time}
                       homeTeam={match.homeTeam}
                       awayTeam={match.awayTeam}
                       match_key={i}
                       isAdmin={this.props.isAdmin}
                       removeMatch={this.removeMatch}
                   />
              )}
            </tbody>
          </table>

    );

    }

}


